import { Link } from 'gatsby';
import React from 'react';
import styled from "styled-components";
import ImageCard from './ImageCard';
import { LastItem } from "../styles/Utility.styled"
import { getImage } from 'gatsby-plugin-image';
import { useStaticQuery, graphql } from "gatsby"

const Section = styled.section`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width: 100%;
    row-gap: var(--m);
    align-items: start;
    //media queries
    @media (max-width: 900px) {
        grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 500px) {
        grid-template-columns: 1fr;
    }  
`;


export default function ImageSection() {
  const data = useStaticQuery(graphql`
    query QueryWorkPreview {
        markdownRemark {
          frontmatter {
            workPreview {
              id
              text1
              text2
              image {
                childImageSharp {
                  gatsbyImageData(width: 400)
                }
              }
            }
          }
        }
      }
      `);
  const workPreview = data.markdownRemark.frontmatter.workPreview;
  return (
    <Section id="images" className="container padding-y-2 margin-bottom" >
      <ImageCard background="#f4f4f4" text1={workPreview[0].text1} text2={workPreview[0].text2} image={getImage(workPreview[0].image)} />
      <ImageCard background="#eaeaea" text1={workPreview[1].text1} text2={workPreview[1].text2} image={getImage(workPreview[1].image)} />
      <ImageCard background="#dedede" text1={workPreview[2].text1} text2={workPreview[2].text2} image={getImage(workPreview[2].image)} />
      <LastItem>
        <p>voir plus ?</p>
        <Link to="/galerie/" className='button-2'>Visiter Gallerie</Link>
      </LastItem>

    </Section>
  );
}

