import React from "react";

export default function ContactSection() {
    return (
        <section id="contact" className="container padding-y-2">
            <div className="grid-split">
                <form
                action="https://formspree.io/f/xwkyolrv"
                method="POST"
                >
                    <input type="text" name="name" placeholder="Votre Nom" />
                    <input type="email" name="email" placeholder="Votre Email" />
                    <textarea rows="5" name="message" placeholder="Votre Message" />
                    <button className="button" type="submit">Envoyer Message</button >
                </form>
                <div className="flex-column gap-m" >
                    <a className="flex-align-center gap-s" href="tel:+33780012233">
                        <img className="font-size-square " src="./svgs/phone.svg" alt="" />
                        <p>
                            0780012233
                        </p>
                    </a>
                    <a className="flex-align-center gap-s" href="mailto:foixelagage@gmail.com">
                    <img className="font-size-square " src="./svgs/mail.svg" alt="" />
                        <p>
                            foixelagage@gmail.com
                        </p>
                    </a>
                    <a className="flex-align-center gap-s" href="/">
                    <img className="font-size-square " src="./svgs/location.svg" alt="" />
                        <p>
                            1 Route de Foix, 09000 Ganac
                        </p>
                    </a>

                </div>

            </div>
        </section>

    )
}