import * as React from "react"
import { StaticImage } from "gatsby-plugin-image"
import Delimiter from "./Delimiter"
import { Hr } from "../styles/Utility.styled"
import { AnchorLink } from "gatsby-plugin-anchor-links"

export default function HeroSection() {
  return (
    <section className="bg-image-wrapper">
      <StaticImage className="bg-image" objectPosition="top" layout="fullWidth" alt="" src="../images/hero.jpg" formats={["auto", "webp", "avif"]} />
      <div className="container text-right full-height flex-column-center-end gap-m white relative">
        <h1 className="page-title">Foix Elagage</h1>
        <Hr />
        <h3 className="page-subtitle">Votre Arboriste Elagueur en Ariège</h3>
        <div className="flex-end gap-m-2q">
          <a className="button-outline" href="tel:+33780012233">0780012233</a>
          <AnchorLink to="/#contact" className="button-outline">Email</AnchorLink>
        </div>        
      <AnchorLink to="/#services" stripHash className="scroll-arrow">
        <img src="./svgs/arrowDown.svg" alt="" />
      </AnchorLink>
      </div>
      <Delimiter bottom left/>
    </section>
  )
}

