import * as React from "react"
import styled from "styled-components"

const CardContainer = styled.div`
display: grid;
grid-template-columns: repeat(auto-fit, minmax(20ch, 1fr));
grid-auto-flow: dense;
align-items: start;
gap: var(--m);
grid-row-gap: var(--m-2);
font-size: var(--step--1);

`

export default function ServiceSection() {
  return (
    <section id='services' className="container padding-y-2">
      <CardContainer>
        <div className="card">
          <img src='./svgs/taille.svg' alt='' />
          <h2>Taille</h2>
          <p>
            Taille de haie, Taille de formation, Taille de cohabitation, Taille de
            sécurisation
          </p>
        </div>
        <div className="card">
          <img src='./svgs/tree.svg' alt='' />
          <h2>Abattage</h2>
          <p>
            Il y a plusieurs options pour diriger la chute de l'arbre :<br />
            -entaille directionnelle <br />
            -coin et Merlin <br />
            -système de corde
          </p>
        </div>
        <div className="card">
          <img src='./svgs/demontage.svg' alt='' />
          <h2>Démontage</h2>
          <p>
            Le démontage d'un arbre consiste à retirer tout ou partie de la couronne
            de l'arbre dressé avant de : <br />
            -l'abattre <br />
            -ou de débiter entièrement l'arbre vertical lorsque l'abattage est
            impossible.
          </p>
        </div>
        <div className="card">
          <img src='./svgs/minipelle.svg' alt="" />
          <h2>Et plus</h2>
          <p>
            aménagement paysager, nettoyage des gouttières, service mini pelle,
            charpenterie...
          </p>
        </div>
      </CardContainer>
    </section>
  )
}

