import * as React from "react"
import { StaticImage } from "gatsby-plugin-image"
import Delimiter from "./Delimiter"


export default function AboutSection() {
  return (
    <section id="about" className="bg-image-wrapper ">
      <StaticImage className="bg-image"  layout="fullWidth" alt="" src="../images/trunk_colorized.jpg" formats={["auto", "webp", "avif"]}/>

      <div >
        <Delimiter top left />
        <div id="aboutFlex" className="container padding-y-3 white">
          <StaticImage style={{borderRadius: "50%", maxWidth:350}} alt="" width={500} src="../images/leon.jpg" formats={["auto", "webp", "avif"]}/>
          <div className="max-width-ch">
            <h5 className="thin uppercase" >Leon Ries</h5>
            <h3>ARBORISTE DIPLOMÉ</h3>
            <p>
              «Pour moi le plus important, c'est de faire un bon travail et que vous soyez satisfait de mon service. Nous terminerons votre chantier au plus vite possible et laisserons vos lieus beaux et propres.»
            </p>
          </div>
        </div>
        <Delimiter bottom right />
      </div>


    </section>
  )
}

